<template>
  <div>
    <el-dialog :title="title" :visible.sync="addVisible" @close="reset()" width="446px">
      <div class="small_form shelfconfig_small">
        <el-form ref="userform" label-width="100px">
          <el-form-item :label="$t('warehouseconfig.name')" prop="add_user_name">
            <el-input v-model="warehouse_name" :placeholder="$t('warehouseconfig.name')"></el-input>
          </el-form-item>
          <el-form-item :label="$t('storage.category_name')">
            <el-select value-key="id" v-model="warehouse_category_id" :placeholder="$t('storage.categoryholder')">
              <el-option v-for="item in categoryTypeList" :key="item.id" :label="item.category_name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="$t('warehouseconfig.warehouselist')">
            <el-checkbox-group v-model="checkedList">
              <el-checkbox v-for="shelf in shelflist" :label="shelf" :key="shelf">{{shelf}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <div class="kutips" v-if="iferror">{{$t('warehouseconfig.nulltips')}}</div>
          <el-form-item>
            <el-button @click="cancel()">{{$t('common.cancel')}}</el-button>
            <el-button type="primary" @click="save()">{{$t('common.save')}}</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {
    searchWarehouseCategory
  } from '@/request/api/materialshelf'; // 导入api接口
  import {
    unboundShelfList,
    addWarehouseName
  } from '@/request/api/materialshelf'; // 导入api接口
  export default {
    props: ['title'],
    data() {
      return {
        addVisible: false,
        warehouse_name: '',
        shelflist: [],
        checkedList: [],
        iferror: false,
        categoryTypeList: [],
        warehouse_category_id: '',
      }
    },
    mounted() {
      this.getUnboundShelfList()
    },
    methods: {
      getUnboundShelfList() {
        unboundShelfList({}).then(res => {
          if (res.result) {
            this.shelflist = res.data
          } else {}
        })
      },
      // 获取库位类型
      getCategoryType() {
        searchWarehouseCategory({}).then(res => {
          if (res.result) {
            this.categoryTypeList = res.data_list
          }
        })
      },
      openDialog(Info) {
        this.addVisible = true
        this.getCategoryType()
      },
      reset() {
        this.warehouse_name = ''
        this.checkedList = []
        this.iferror = false
      },
      cancel() {
        this.addVisible = false
        this.iferror = false
        this.warehouse_name = ''
        this.checkedList = []
      },
      save() {
        if (!this.warehouse_name || !this.checkedList.length || !this.warehouse_category_id) {
          this.iferror = true
          return false
        }
        addWarehouseName({
          warehouse_name: this.warehouse_name,
          shelf_list: this.checkedList,
          warehouse_category_id: this.warehouse_category_id
        }).then(res => {
          if (res.result) {
            this.$emit('getwarehouseList')
            this.addVisible = false
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      }
    }
  }
</script>
<style>
  .shelfconfig_small .el-checkbox-group {
    max-height: 80px;
    overflow-y: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .shelfconfig_small .el-checkbox {
    margin-right: 20px;
  }

  .kutips {
    height: 30px;
    line-height: 30px;
    color: red;
    text-align: left;
  }
</style>



