<template>
  <div>
    <div class="warehouseheader">
      <el-button type="success" @click="Add">{{$t('warehouseconfig.add')}}</el-button>
    </div>
    <v-edit ref="edit" v-on:getwarehouseList="getwarehouseList" :title="title"></v-edit>
    <template>
      <el-table :header-cell-style="{background:'#DFEBF9',color:'#606266'}" :loading="loading" :data="warehouse_list"
        :empty-text="$t('common.nodata')" border>
        <el-table-column :label="$t('common.No')" type="index" width="50">
        </el-table-column>
        <el-table-column prop="warehouse_category_name" label="库位类别">
        </el-table-column>
        <el-table-column prop="warehouse_name" label="库位">
        </el-table-column>
        <el-table-column prop="shelf_list" label="料架">
        </el-table-column>
        <el-table-column prop="" :label="$t('common.operation')" width="158">
          <template slot-scope="scope">
            <el-button type='primary' size="mini" @click="editWarehouse(scope.row)" :disabled="permissionFlag">{{$t('warehouseconfig.edit')}}</el-button>
            <el-button type='danger' size="mini" @click="deleteWarehouse(scope.row)" :disabled="permissionFlag">{{$t('common.delete')}}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
    <el-dialog :title="title" :visible.sync="editVisible" width="446px">
      <div class="small_form shelfconfig_small">
        <el-form ref="userform" label-width="100px">
          <el-form-item :label="$t('warehouseconfig.name')" prop="add_user_name">
            <el-input v-model="warehouseform.warehouse_name"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="editCancel">{{$t('common.cancel')}}</el-button>
            <el-button type="primary" @click="editSave">{{$t('common.save')}}</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  // import VUseredit from '@/components/user/commonuser/useredit'
  import VEdit from './edit'
  import {
    permission
  } from '@/utils/public'
  import {
    warehouseCategoryNameList,
    updateWarehouseName,
    delWarehouseName
  } from '@/request/api/materialshelf'; // 导入api接口
  export default {
    components: {
      VEdit
    },
    data() {
      return {
        loading: true,
        editVisible: false,
        permissionFlag: permission(),
        warehouse_list: [],
        searchholder: this.$t('user.searchholder'),
        title: '',
        warehouseform: {
          warehouse_name: '',
          warehouse_name_id: '',
        }
      }
    },
    created() {

    },
    mounted() {
      this.getwarehouseList();
    },
    methods: {
      // 获取库位列表
      getwarehouseList() {
        warehouseCategoryNameList({}).then(res => {
          if (res.result) {
            this.warehouse_list = res.data_list
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      Add() {
        this.title = this.$t('warehouseconfig.addtitle')
        this.$refs.edit.openDialog()
      },
      editWarehouse(Info) {
        this.editVisible = true,
          this.title = this.$t('warehouseconfig.edittitle')
        this.warehouseform.warehouse_name_id = Info.warehouse_name_id
        this.warehouseform.warehouse_name = Info.warehouse_name
      },
      deleteWarehouse(value) {
        this.$confirm(this.$t('user.deltips'), this.$t('user.tips'), {
          confirmButtonText: this.$t('common.sure'),
          cancelButtonText: this.$t('common.cancel'),
          type: 'warning'
        }).then(() => {
          delWarehouseName({
            warehouse_name: value.warehouse_name,
            warehouse_name_id: value.warehouse_name_id,
            warehouse_category_id: value.warehouse_category_id,
            warehouse_category_name: value.warehouse_category_name
          }).then(res => {
            if (res.result) {
              this.getwarehouseList()
            }
          })
        }).catch(() => {

        });
      },
      editCancel() {
        this.editVisible = false
      },
      editSave() {
        updateWarehouseName(this.warehouseform).then(res => {
          if (res.result) {
            this.editVisible = false
            this.$message({
              showClose: true,
              message: '库位更新成功',
              type: 'success'
            })
            this.getwarehouseList()
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      }
    }
  }
</script>
<style>
  .warehouseheader {
    text-align: left;
    margin: 10px;
  }
</style>



